*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: "montserait", sans-serif;
}

.app{
  background-image: url("./image/warm.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.5 ease-out;
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));;
  padding: 25px;
}

.app.warm{
  background-image: url("./image/warm.jpg");
}

.app.cold{
  background-image: url("./image/cold.jpg");
}

.app.rain{
  background-image: url("./image/rain.jpg");
}

.app.cloud{
  background-image: url("./image/cloud.jpg");
}

.app.clear{
  background-image: url("./image/clear.jpg");
}

.app.normal{
  background-image: url('./image/normal.jpg');
}

.app.mist{
  background-image: url('./image/mist.jpg');
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 0 0 16px 16px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.6s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
  font-style: italic;
  padding-top: 10px;
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 100px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 4px 6px rgba(0,0,0,0.3);
}

.weather-box .weather{
  color: #fff;
  font-size: 40px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}